// sliders
function mainSlider() {
    var swiper = new Swiper('.main .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 10,
        loop: true,
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        pagination: {
            el: '.main .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

function brandsSlider() {
    var swiper = new Swiper('.brands .swiper-container', {
        slidesPerView: 'auto',
        spaceBetween: 0,
        navigation: {
            nextEl: '.brands .swiper-button-next',
            prevEl: '.brands .swiper-button-prev',
        },
        scrollbar: {
            el: '.brands .swiper-scrollbar',
            draggable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1.7,
            },
            576: {
                slidesPerView: 'auto',
            }
        }
    })
}

function servicesSlider() {
    var swiper = new Swiper('.related-service .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
    })
}

function historySlider() {
    var swiper = new Swiper('.history .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
    })
}
function productSlider() {
    var swiper = new Swiper('.product-modal .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        loop: true,
        navigation: {
            nextEl: '.product-modal .swiper-button-next',
            prevEl: '.product-modal .swiper-button-prev',
        },
    })
}

function collectionSlider() {
    var swiper = new Swiper('.product-collection .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
    })
}

function tariffsSlider() {
    var swiper = new Swiper('.tariffs .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
    })
}


mainSlider()
brandsSlider()
servicesSlider()
historySlider()
productSlider()
collectionSlider()
tariffsSlider()
// end sliders
